* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Vazir, sans-serif;
  line-height: 1.5;
  padding: 3rem;
  background-color: #f4f4f4;
  direction: rtl;
  text-align: right;
}

.container {
  max-width: 576px !important;
}

label {
  display: block;
  text-align: right !important;
}

.app {
  min-height: 500px;
  text-align: center;
  padding: 0.75rem 1rem;
}

.stage-progress {
  padding: 0;
  margin-bottom: 2.5rem;
}

.stage-progress .progress {
  height: 5px;
}

.stage-progress .stages {
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translateY(-0.9rem);
}

.stage-progress .stage-name {
  position: absolute;
  bottom: -1rem;
  text-align: center;
}


.stage-progress .stages-title > div {
  text-align: center;
  width: calc(100% / 5);
}

.stage-progress .stages .badge {
  padding: 0.4rem;
}

.stage-progress .stages .stage-name {
  font-size: 0.6rem;
}

.app .card-header,
.app .card-footer {
  background-color: transparent;
  border-bottom: none;
}

.app .card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app .prev-step,
.app .next-step {
  padding: 0.4rem 2rem;
}
